import * as React from 'react';
import { Modal, Button, Fade } from '@material-ui/core';

import './LearnMoreModal.scss';

type IProps = {
  visible: boolean;
  onClose: () => void;
};

const GetUIdModal: React.SFC<IProps> = (props) => {
  const render = (
    <Modal
      disablePortal
      disableEnforceFocus
      disableAutoFocus
      disableBackdropClick
      aria-labelledby={'server-modal-title'}
      aria-describedby={'server-modal-description'}
      onClose={props.onClose}
      open={props.visible}
      style={{ outline: 0 }}
      className={'learn-more-modal'}
    >
      <Fade in={props.visible}>
        <div className={'learn-more-modal-container'}>
          <h2>不知道写啥，就这样吧</h2>
          <h3>我们会严格遵守相关数据保密工作</h3>
          <h3>请放心使用</h3>
          <Button
            variant={'contained'}
            color={'primary'}
            onClick={props.onClose}
          >
            返回
          </Button>
        </div>
      </Fade>
    </Modal>
  );

  return render;
};

export default GetUIdModal;
