import * as React from 'react';
import { Button, TextField } from '@material-ui/core';
import { Delete as DeleteIcon } from '@material-ui/icons';

import { DELETE_USER } from '../../api';

import './Delete.scss';

export const CREATE_USER_MESSAGE = {
  'failedGh.html': '任务提交失败，请检查学号',
  'deleteFailed.html': '删除任务失败',
  'deleteSuccess.html': '删除任务成功',
};

const Delete: React.SFC<any> = (props) => {
  const [sid, setSid] = React.useState<string>('');

  const checkStudentID = React.useCallback((id: string): boolean => {
    return /^\d{11}$/.test(id);
  }, []);

  const handleSubmit = React.useCallback(
    async (ev: React.FormEvent<HTMLFormElement>) => {
      ev.preventDefault();
      ev.stopPropagation();

      if (!checkStudentID(sid)) {
        props.setError('学号格式错误');
        return;
      }

      try {
        const res = await DELETE_USER({ gh: sid });
        if (res.status !== 200) {
          throw new Error('网络错误');
        }
        const code = res.data.code;
        const message: keyof typeof CREATE_USER_MESSAGE = res.data.message;
        if (code == 1000) {
          // 成功
          props.setSuccess(CREATE_USER_MESSAGE[message] || '设置打卡任务成功');
        } else {
          // 失败
          throw new Error(CREATE_USER_MESSAGE[message] || '未知错误');
        }
      } catch (error) {
        console.error(error);
        props.setError(error.message || JSON.stringify(error));
      } finally {
        props.setLoading(false);
      }
    },
    [sid]
  );

  const renderSIDInput = React.useMemo(
    () => (
      <TextField
        value={sid}
        onChange={(val) => setSid(val.target.value)}
        fullWidth
        required
        label={'您的学号'}
      />
    ),
    [sid]
  );

  const render = (
    <React.Fragment>
      <form onSubmit={handleSubmit} className={'delete-container'}>
        <ul>
          <li>{renderSIDInput}</li>
          <li>
            <Button
              variant={'contained'}
              color={'secondary'}
              type={'submit'}
              endIcon={<DeleteIcon />}
              fullWidth
            >
              删除任务
            </Button>
          </li>
        </ul>
      </form>
    </React.Fragment>
  );
  return render;
};

export default Delete;
