import * as React from 'react';
import {
  Modal,
  Step,
  Stepper,
  StepLabel,
  Button,
  Fade,
} from '@material-ui/core';

import FirstImage from '../../common/images/1.jpg';
import SecondImage from '../../common/images/2.jpg';
import ThirdImage from '../../common/images/3.jpg';

import './GetUIdModal.scss';

type IProps = {
  visible: boolean;
  onClose: () => void;
};

const steps = ['进入主页', '点击更多', '复制链接'];
const stepImages = [FirstImage, SecondImage, ThirdImage].map((i) => (
  <img src={i} />
));

const GetUIdModal: React.SFC<IProps> = (props) => {
  const [activeStep, setActiveStep] = React.useState<number>(0);

  const render = (
    <Modal
      disablePortal
      disableEnforceFocus
      disableAutoFocus
      disableBackdropClick
      aria-labelledby={'server-modal-title'}
      aria-describedby={'server-modal-description'}
      onClose={props.onClose}
      open={props.visible}
      style={{ outline: 0 }}
      className={'get-uid-modal'}
    >
      <Fade in={props.visible}>
        <div className={'get-uid-modal-container'}>
          <Stepper
            className={'get-uid-modal-stepper'}
            orientation={'vertical'}
            activeStep={activeStep}
          >
            {steps.map((item) => (
              <Step key={item}>
                <StepLabel>{item}</StepLabel>
              </Step>
            ))}
          </Stepper>
          <div className={'get-uid-modal-view'}>
            <div className={'get-uid-modal-view-image'}>
              {stepImages[activeStep]}
            </div>
            <div className={'get-uid-modal-view-buttons'}>
              <Button
                onClick={() => setActiveStep((i) => i - 1)}
                disabled={activeStep === 0}
              >
                上一步
              </Button>
              <Button
                onClick={() => setActiveStep((i) => i + 1)}
                disabled={activeStep === steps.length - 1}
              >
                下一步
              </Button>
              <Button
                onClick={props.onClose}
                variant={'contained'}
                color={'primary'}
              >
                我知道了
              </Button>
            </div>
          </div>
        </div>
      </Fade>
    </Modal>
  );

  return render;
};

export default GetUIdModal;
