import * as React from 'react';
import {
  TextField,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  Tooltip,
  IconButton,
} from '@material-ui/core';
import { Save, Help } from '@material-ui/icons';

import './Submit.scss';

import { CREATE_USER } from '../../api';

export const CREATE_USER_MESSAGE = {
  'failed.html': '任务提交失败，请检查 Uid',
  'failedGh.html': '任务提交失败，请检查学号',
  'failedIn.html': '任务提交失败，没有选择打卡时间',
  'success.html': '创建任务成功',
};

const Submit: React.SFC<any> = (props) => {
  const [uid, setUid] = React.useState<string>(
    'https://ehallplatform.xust.edu.cn/default/jkdk/mobile/mobJkdkAdd_test.jsp?uid=MDgxNEUxNUJEMEI0QUY0NTE2MkExNDQ3RTQ2NTlEN0I='
  );
  const [sid, setSid] = React.useState<string>('17408070128');
  const [email, setEmail] = React.useState<string>('example@163.com');
  const [count, setCount] = React.useState<string>('once');

  const checkUID = React.useCallback((id: string): boolean => {
    // http://ehallplatform.xust.edu.cn/default/jkdk/mobile/mobJkdkAdd_test.jsp?uid=*******=
    return /^https?:\/\/ehallplatform\.xust\.edu\.cn\/default\/jkdk\/mobile\/mobJkdkAdd_test\.jsp\?uid=.+/.test(
      id
    );
  }, []);

  const checkStudentID = React.useCallback((id: string): boolean => {
    return /^\d{11}$/.test(id);
  }, []);

  const checkEmail = React.useCallback((email: string): boolean => {
    return /^[A-Za-z0-9\u4e00-\u9fa5_]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(
      email
    );
  }, []);

  const handleSubmit = React.useCallback(
    async (ev: React.FormEvent<HTMLFormElement>) => {
      ev.preventDefault();
      ev.stopPropagation();

      if (!checkUID(uid)) {
        props.setError('UID 格式错误');
        return;
      }
      if (!checkStudentID(sid)) {
        props.setError('学号格式错误');
        return;
      }
      if (!checkEmail(email)) {
        props.setError('邮箱格式错误');
        return;
      }
      if (!count || !['once', 'twice'].includes(count)) {
        props.setError('请选择打卡次数');
        return;
      }

      const data = {
        uid,
        gh: sid,
        in: count === 'once' ? '1' : '2',
        email,
      };

      try {
        props.setLoading(true);
        const res = await CREATE_USER(data);
        if (res.status !== 200) {
          throw new Error('网络错误');
        }
        const code = res.data.code;
        const message: keyof typeof CREATE_USER_MESSAGE = res.data.message;

        if (code == 1000) {
          // 成功
          props.setSuccess(CREATE_USER_MESSAGE[message] || '设置打卡任务成功');
        } else {
          // 失败
          throw new Error(CREATE_USER_MESSAGE[message] || '未知错误');
        }
      } catch (error) {
        console.error(error);
        props.setError(error.message || JSON.stringify(error));
      } finally {
        props.setLoading(false);
      }
    },
    [uid, sid, email, count]
  );

  const renderUIDInput = React.useMemo(
    () => (
      <React.Fragment>
        <TextField
          value={uid}
          onChange={(val) => setUid(val.target.value)}
          label={'您的 UID'}
          fullWidth
          required
        />
        <Tooltip title={'怎么获取 UID？'} arrow>
          <IconButton
            onClick={props.openGetUidModal}
            color="primary"
            component="span"
          >
            <Help />
          </IconButton>
        </Tooltip>
      </React.Fragment>
    ),
    [uid]
  );

  const renderSIDInput = React.useMemo(
    () => (
      <TextField
        value={sid}
        onChange={(val) => setSid(val.target.value)}
        fullWidth
        required
        label={'您的学号'}
      />
    ),
    [sid]
  );

  const renderEmailInput = React.useMemo(
    () => (
      <TextField
        value={email}
        onChange={(val) => setEmail(val.target.value)}
        fullWidth
        required
        label={'您的邮箱'}
      />
    ),
    [email]
  );

  const renderSelectorInput = React.useMemo(
    () => (
      <React.Fragment>
        <RadioGroup
          className={'count-selection'}
          value={count}
          onChange={(_, val) => setCount(val)}
          row
        >
          <FormControlLabel
            value="twice"
            control={<Radio color={'primary'} />}
            label="午检+晚检"
          />
          <FormControlLabel
            value="once"
            control={<Radio color={'primary'} />}
            label="晚检"
          />
        </RadioGroup>
      </React.Fragment>
    ),
    [count]
  );

  const render = (
    <React.Fragment>
      <form onSubmit={handleSubmit} className={'submit-container'}>
        <ul>
          <li>{renderUIDInput}</li>
          <li>{renderSIDInput}</li>
          <li>{renderEmailInput}</li>
          <li>{renderSelectorInput}</li>
          <li>
            <Button
              variant={'contained'}
              color={'primary'}
              type={'submit'}
              endIcon={<Save />}
              fullWidth
            >
              提交任务
            </Button>
          </li>
        </ul>
      </form>
    </React.Fragment>
  );

  return render;
};

export default Submit;
