import * as React from 'react';
import {
  AppBar,
  Tabs,
  Tab,
  Snackbar,
  Backdrop,
  CircularProgress,
} from '@material-ui/core';
import SwipeableViews from 'react-swipeable-views';
import { Alert } from '@material-ui/lab';

import Submit from '../../components/Submit/Submit';
import Delete from '../../components/Delete/Delete';
import GetUidModal from '../../components/GetUIdModal/GetUIdModal';
import LearnMoreModal from '../../components/LearnMoreModal/LearnMoreModal';

import './Home.scss';

const App: React.FC<{}> = () => {
  const [current, setCurrent] = React.useState<number>(0);
  const [
    errorSnackbarMessage,
    setErrorSnackbarMessage,
  ] = React.useState<string>('');
  const [
    successSnackbarMessage,
    setSuccessSnackbarMessage,
  ] = React.useState<string>('');
  const [getUidModalVisible, setGetUidModalVisible] = React.useState<boolean>(
    false
  );
  const [
    learnMoreModalVisible,
    setLearnMoreModalVisible,
  ] = React.useState<boolean>(false);
  const [loading, setLoading] = React.useState<boolean>(false);

  const tabSelection = React.useMemo(
    () => [
      {
        label: '提交任务',
        container: (
          <Submit
            openGetUidModal={() => setGetUidModalVisible(true)}
            setError={setErrorSnackbarMessage}
            setSuccess={setSuccessSnackbarMessage}
            setLoading={setLoading}
          />
        ),
      },
      {
        label: '删除任务',
        container: (
          <Delete
            setSuccess={setSuccessSnackbarMessage}
            setLoading={setLoading}
            setError={setErrorSnackbarMessage}
          />
        ),
      },
    ],
    []
  );

  const handleCurrentChange = React.useCallback((_, value) => {
    setCurrent(value);
  }, []);

  const renderErrorSnackbar = React.useMemo(
    () => (
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={
          typeof errorSnackbarMessage === 'string' &&
          !!errorSnackbarMessage.length
        }
        autoHideDuration={3000}
      >
        <Alert
          variant={'filled'}
          elevation={6}
          onClose={() => setErrorSnackbarMessage('')}
          severity={'error'}
        >
          {errorSnackbarMessage}
        </Alert>
      </Snackbar>
    ),
    [errorSnackbarMessage]
  );

  const renderSuccessSnackbar = React.useMemo(
    () => (
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        open={
          typeof successSnackbarMessage === 'string' &&
          !!successSnackbarMessage.length
        }
        autoHideDuration={3000}
      >
        <Alert
          variant={'filled'}
          elevation={6}
          onClose={() => setSuccessSnackbarMessage('')}
          severity={'success'}
        >
          {successSnackbarMessage}
        </Alert>
      </Snackbar>
    ),
    [successSnackbarMessage]
  );

  return (
    <React.Fragment>
      <div className={'box-main'}>
        <AppBar position="static" color="default">
          <Tabs
            value={current}
            onChange={handleCurrentChange}
            indicatorColor="primary"
            textColor="primary"
            variant="fullWidth"
            aria-label="full width tabs example"
          >
            {tabSelection.map((item) => (
              <Tab label={item.label} />
            ))}
          </Tabs>
        </AppBar>
        <SwipeableViews className={'container'} index={current}>
          {tabSelection.map((item) => item.container)}
        </SwipeableViews>
      </div>
      <footer className={'main-footer'}>
        <p>
          &copy; 默默无闻的开发者们{' '}
          <span onClick={() => setLearnMoreModalVisible(true)}>了解更多</span>
        </p>
      </footer>
      {renderErrorSnackbar}
      {renderSuccessSnackbar}
      <GetUidModal
        visible={getUidModalVisible}
        onClose={() => setGetUidModalVisible(false)}
      />
      <LearnMoreModal
        visible={learnMoreModalVisible}
        onClose={() => setLearnMoreModalVisible(false)}
      />
      <Backdrop className={'main-backdrop'} open={loading}>
        <CircularProgress color={'secondary'} />
      </Backdrop>
    </React.Fragment>
  );
};

export default App;
