import axios, { AxiosResponse } from 'axios';

export interface RESPONSE_DATA {
  status_code: number;
  data?: any;
  e?: any;
}

axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    if (error.response) {
      return error.response.data;
    }
    return error;
  }
);

type AxiosRequest = {
  method?: 'get' | 'GET' | 'post' | 'POST';
  url: string;
  params?: {};
  data?: {};
};

const prefix =
  process.env.NODE_ENV === 'development' ? 'http://192.168.28.33:8080' : '';

const request: (params: AxiosRequest) => Promise<AxiosResponse<any>> = (
  params
) =>
  axios({
    method: params.method || 'get',
    url: `${prefix}${params.url}`,
    params: params.params || {},
    data: params.data || {},
    // timeout: 15000,
  });

export const CREATE_USER = (data: any) =>
  request({
    url: `/createUser`,
    data,
    method: 'post',
  });

export const DELETE_USER = (data: any) =>
  request({
    url: `/delete`,
    data,
    method: 'post',
  });
